import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the liveMessageContainer state domain
 */
const selectLiveMessageContainerDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const makeSelectLiveMessages = () =>
  createSelector(
    selectLiveMessageContainerDomain(),
    (substate) => substate.liveMessages,
  );

const makeSelectShowFeed = () =>
  createSelector(
    selectLiveMessageContainerDomain(),
    (substate) => substate.showFeed,
  );

const makeSelectIsSubscribed = () =>
  createSelector(
    selectLiveMessageContainerDomain(),
    (substate) => substate.isSubscribed,
  );

/**
 * Default selector used by LiveMessageContainer
 */

const makeSelectLiveMessageContainer = () =>
  createSelector(selectLiveMessageContainerDomain(), (substate) => substate);

export default makeSelectLiveMessageContainer;
export {
  selectLiveMessageContainerDomain,
  makeSelectLiveMessages,
  makeSelectShowFeed,
  makeSelectIsSubscribed,
};



