/*
 *
 * LiveMessageContainer
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import { CBDispatch } from 'cb-utils/typeUtils';
import {
  makeSelectLiveMessages,
  makeSelectShowFeed,
  makeSelectIsSubscribed,
} from './selectors';
import {
  createExpandFeed,
  createHideFeed,
  createSubscribeMQTT,
  createUnsubscribeMQTT,
  createClearLiveMessages,
  createSendTestMessageRequest,
} from './actions';
import LiveMessagesFeed from 'components/LiveMessageFeed';
import TestMessageFeed from 'components/TestMessageFeed';
import { createStructuredSelector } from 'reselect';

interface LiveMessageContainerProps {
  topicName?: string;
  allTopicNames?: string[];
}

export type MasterProps = LiveMessageContainerProps &
  DispatchToProps &
  StateToProps;

export class LiveMessageContainer extends React.PureComponent<MasterProps, {}> {
  render() {
    return this.props.topicName ? (
      <LiveMessagesFeed {...this.props} />
    ) : (
      <TestMessageFeed {...this.props} />
    );
  }
}

const mapStateToProps = createStructuredSelector({
  liveMessages: makeSelectLiveMessages(),
  showFeed: makeSelectShowFeed(),
  isSubscribed: makeSelectIsSubscribed(),
});

function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: LiveMessageContainerProps,
) {
  return {
    clearLiveMessages: () => dispatch(createClearLiveMessages()),
    expandFeed: () => dispatch(createExpandFeed()),
    closeModal: () => dispatch(createHideFeed()),
    subscribe: (topic?: string) =>
      dispatch(createSubscribeMQTT(topic || ownProps.topicName)),
    unsubscribe: (topic?: string) =>
      dispatch(createUnsubscribeMQTT(topic || ownProps.topicName)),
    sendMessage: (topic: string, msg: string) =>
      dispatch(createSendTestMessageRequest(topic, msg)),
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
type StateToProps = ReturnType<typeof mapStateToProps>;

const withConnect = connect<StateToProps, DispatchToProps>(
  mapStateToProps,
  mapDispatchToProps,
);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(
  LiveMessageContainer,
) as React.ComponentType<LiveMessageContainerProps>;



