/*
 *
 * MessageQueryContainer
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import {
  selectTimeframe,
  selectCount,
  selectStart,
  selectStop,
  selectLast,
} from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import { ButtonGroup, Button, Input } from 'reactstrap';
import {
  createChangeTimeConstraint,
  createChangeCount,
  createChangeLast,
  createChangeStart,
  createChangeStop,
  createClearQueries,
} from 'containers/MessageQueryContainer/actions';

import CbDatePicker from 'components/CbDatePicker';
import { createStructuredSelector } from 'reselect';

const Row = styled.div`
  display: flex;
`;

const Buttons = styled(ButtonGroup)`
  margin-left: 1rem;
`;

const InputWrap = styled.div`
  max-width: 10rem;
  display: flex;
  flex-direction: column;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
`;

const ButtonWrap = styled(Button)`
  margin-top: 1rem;
  margin-right: 0.5rem;
`;

interface MessageQueryContainerProps {
  fetchMessages: () => void;
  deleteMessages: () => void;
}

export class MessageQueryContainer extends React.Component<
  MessageQueryContainerProps & StateToProps & DispatchToProps,
  {}
> {
  componentWillMount() {
    const newDate = new Date().getTime();
    this.props.changeLast(Math.ceil(newDate));
  }

  componentWillUnmount() {
    this.props.clearQueries();
  }

  render() {
    return (
      <div className="mr-5">
        <Row>
          <h5>
            <FormattedMessage {...messages.filters} />
          </h5>
          <Buttons size={'sm'}>
            <Button
              outline
              color="primary"
              active={!this.props.isTimeframe}
              onClick={() => this.props.changeTimeConstraint(false)}
            >
              <FormattedMessage {...messages.timestamp} />
            </Button>
            <Button
              outline
              color="primary"
              active={this.props.isTimeframe}
              onClick={() => this.props.changeTimeConstraint(true)}
            >
              <FormattedMessage {...messages.timeframe} />
            </Button>
          </Buttons>
        </Row>
        <Row>
          <InputWrap>
            {this.props.isTimeframe ? (
              <FormattedMessage {...messages.from} />
            ) : (
              <FormattedMessage {...messages.before} />
            )}
            <CbDatePicker
              onChange={({ target }) => {
                this.props.isTimeframe
                  ? this.changeDateValue(this.props.changeStart, target.value)
                  : this.changeDateValue(this.props.changeLast, target.value);
              }}
              showTime
              value={
                this.props.isTimeframe
                  ? this.props.startDate
                  : this.props.lastDate
              }
            />
          </InputWrap>
          {this.props.isTimeframe ? (
            <InputWrap>
              <FormattedMessage {...messages.to} />
              <CbDatePicker
                onChange={({ target }) => {
                  this.changeDateValue(this.props.changeStop, target.value);
                }}
                showTime
                value={this.props.stopDate}
              />
            </InputWrap>
          ) : null}
        </Row>
        <Row>
          <InputWrap>
            <FormattedMessage {...messages.count} />
            <Input
              type="number"
              onChange={(e: any) => this.changeCountValue(e.target.value)}
              value={this.props.count}
            />
          </InputWrap>
        </Row>
        <Row>
          <ButtonWrap
            size="sm"
            color="primary"
            onClick={this.props.fetchMessages}
          >
            Fetch
          </ButtonWrap>
          <ButtonWrap
            size={'sm'}
            color="danger"
            onClick={this.props.deleteMessages}
          >
            Delete
          </ButtonWrap>
        </Row>
      </div>
    );
  }

  changeDateValue(fn: (value: number) => void, dateString: string) {
    const timeValue = new Date(dateString).getTime();
    fn(timeValue);
  }

  changeCountValue(value: string) {
    const numValue = Number(value);
    this.props.changeCount(numValue);
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  isTimeframe: selectTimeframe(),
  lastDate: selectLast(),
  startDate: selectStart(),
  stopDate: selectStop(),
  count: selectCount(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

const mapDispatchToProps = (dispatch: CBDispatch) => {
  return {
    changeTimeConstraint: (isTimeframe: boolean) =>
      dispatch(createChangeTimeConstraint(isTimeframe)),
    changeCount: (count: number) => dispatch(createChangeCount(count)),
    changeLast: (last: number) => dispatch(createChangeLast(last)),
    changeStart: (start: number) => dispatch(createChangeStart(start)),
    changeStop: (stop: number) => dispatch(createChangeStop(stop)),
    clearQueries: () => dispatch(createClearQueries()),
  };
};

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
export default compose(withReducer, withConnect)(
  MessageQueryContainer,
) as React.ComponentType<MessageQueryContainerProps>;



