/*
*
* TestMessageFeed
*
*/

import React from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import liveMessages from 'components/LiveMessageFeed/messages';
import { MasterProps } from 'containers/LiveMessageContainer';
import CbTypeahead from 'components/CbTypeahead';
import { InputGroup, Input, Button, InputGroupAddon } from 'reactstrap';
import { FeedDisplay, LiveMessagesModal } from 'components/LiveMessageFeed';
import { ExpandIcon } from 'components/EntityIcons';

const InputWrapper = styled(InputGroup)`
  display: flex;
  > :first-child {
    flex-grow: 1;
  }
`;

const FeedWrap = styled.div`
  display: flex !important;
  flex-direction: column;
  height: 100%;
`;

export const isWildcard = (input: string) => {
  return input.length > 0 && (input.includes('#') || input.includes('+'));
};

interface TestMessageFeedState {
  topicName: string;
  message: string;
}

class TestMessageFeed extends React.Component<
  MasterProps,
  TestMessageFeedState
> {
  state = {
    topicName: '',
    message: '',
  };

  componentWillUnmount() {
    if (this.props.isSubscribed) {
      this.props.unsubscribe(this.state.topicName);
    }
    this.props.clearLiveMessages();
  }

  render() {
    return (
      <FeedWrap>
        <div>
          <h4 className="mt-4">
            <FormattedMessage {...messages.topic} />
          </h4>
          <InputWrapper>
            <CbTypeahead
              options={this.props.allTopicNames.map((topic: string) => {
                return {
                  name: topic,
                  value: topic,
                };
              })}
              onChange={this.handleTopicChange}
              onInputChange={this.handleTopicChange}
              allowNew={false /* is uncontrolled so already allows it*/}
              emptyLabel="Enter topic name or wildcard"
            />
            <InputGroupAddon addonType="append">
              <Button
                color="primary"
                onClick={
                  this.props.isSubscribed
                    ? () => this.props.unsubscribe(this.state.topicName)
                    : () => this.props.subscribe(this.state.topicName)
                }
              >
                {this.props.isSubscribed ? (
                  <FormattedMessage {...liveMessages.unsubscribe} />
                ) : (
                  <FormattedMessage {...liveMessages.subscribe} />
                )}
              </Button>
            </InputGroupAddon>
          </InputWrapper>
        </div>
        <div>
          <h4 className="mt-4">
            <FormattedMessage {...messages.message} />
          </h4>
          <InputWrapper>
            <Input onChange={(e) => this.handleMessageChange(e.target.value)} />
            <InputGroupAddon addonType="append">
              <Button
                color="primary"
                onClick={() =>
                  this.props.sendMessage(
                    this.state.topicName,
                    this.state.message,
                  )
                }
                disabled={
                  !this.props.isSubscribed || isWildcard(this.state.topicName)
                }
                type="submit"
              >
                <FormattedMessage {...messages.publish} />
              </Button>
            </InputGroupAddon>
          </InputWrapper>
        </div>
        <div className="mt-4 d-flex">
          <h4>
            <FormattedMessage {...messages.receivedMessages} />
          </h4>
          <Button
            className="ml-auto"
            onClick={() => this.props.expandFeed()}
            disabled={this.props.liveMessages.length === 0}
          >
            <ExpandIcon />
          </Button>
        </div>
        <FeedDisplay>{this.renderLiveMessages()}</FeedDisplay>
        <LiveMessagesModal
          liveMessages={this.props.liveMessages}
          topicName={this.state.topicName}
          closeModal={this.props.closeModal}
          showFeed={this.props.showFeed}
        />
      </FeedWrap>
    );
  }

  renderLiveMessages = () => {
    return this.props.liveMessages.map((msg: string, idx: number) => (
      <div key={idx}>{msg}</div>
    ));
  };

  handleTopicChange = (val: string) => {
    if (this.props.isSubscribed) {
      this.props.unsubscribe(this.state.topicName);
    }
    this.setState({
      topicName: val,
    });
  };

  handleMessageChange = (val: string) => {
    this.setState({
      message: val,
    });
  };
}

export default TestMessageFeed;



