import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the messageQueryContainer state domain
 */
const selectMessageQueryContainerDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

const selectCount = () =>
  createSelector(
    selectMessageQueryContainerDomain(),
    (substate) => substate.count,
  );
const selectTimeframe = () =>
  createSelector(
    selectMessageQueryContainerDomain(),
    (substate) => substate.isTimeframe,
  );
const selectLast = () =>
  createSelector(
    selectMessageQueryContainerDomain(),
    (substate) => substate.lastDate,
  );
const selectStart = () =>
  createSelector(
    selectMessageQueryContainerDomain(),
    (substate) => substate.startDate,
  );
const selectStop = () =>
  createSelector(
    selectMessageQueryContainerDomain(),
    (substate) => substate.stopDate,
  );

/**
 * Default selector used by MessageQueryContainer
 */

const makeSelectMessageQueryContainer = () =>
  createSelector(selectMessageQueryContainerDomain(), (substate) => substate);

export default makeSelectMessageQueryContainer;
export {
  selectMessageQueryContainerDomain,
  selectTimeframe,
  selectCount,
  selectLast,
  selectStart,
  selectStop,
};



