import { all, takeEvery, call, put } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import { SubscribeMQTT, SendTestMessageRequest } from './actions';
import { subscribeMessage, publishMessage } from 'cb-utils/services/messages';
import { createNewNotification } from 'containers/Console/actions/ui';

function* callSubscribeMQTT(action: SubscribeMQTT) {
  yield call(subscribeMessage, action.payload.topic);
}

function* callSendTestMessage(action: SendTestMessageRequest) {
  try {
    yield call(publishMessage, action.payload.topic, action.payload.msg);
    yield put(
      createNewNotification({
        title: 'Message successfully sent',
      }),
    );
  } catch (e) {
    yield put(
      createNewNotification({
        title: 'Message was not successfully sent',
        className: 'error',
      }),
    );
    throw new Error(e);
  }
}

// Individual exports for testing

export function* subscribeMQTT() {
  yield takeEvery(ActionConstants.SUBSCRIBE_MQTT_REQUEST, callSubscribeMQTT);
}

export function* sendTestMessage() {
  yield takeEvery(
    ActionConstants.SEND_TEST_MESSAGE_REQUEST,
    callSendTestMessage,
  );
}

// All sagas to be loaded
export default function*() {
  yield all([subscribeMQTT(), sendTestMessage()]);
}



