/*
 *
 * MessagesDetail
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import Helmet from 'react-helmet';
import injectSaga from 'utils/injectSaga';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import { makeSelectMessages, makeSelectIsLoading } from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import { CBDispatch } from 'cb-utils/typeUtils';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import ConsolePage from 'components/ConsolePage';
import PageHeader from 'components/ConsolePageHeader';
import MessageHistoryGrid from 'components/MessageHistoryGrid';
import {
  createFetchMessagesRequest,
  createDeleteMessagesRequest,
  createClearMessages,
} from './actions';
import {
  createShowBaseModal,
  createShowConfirmationModal,
} from 'containers/Console/actions/ui';
import { CustomModalBodyComponents } from 'components/BaseModal';
import MqttConnection from 'containers/MqttConnection';
import MessageQueryContainer from 'containers/MessageQueryContainer';
import messages from './messages';
import { FormattedMessage } from 'react-intl';
import LiveMessageContainer from 'containers/LiveMessageContainer';
import { isWildcard } from 'components/TestMessageFeed';
import { createStructuredSelector } from 'reselect';
import { MessageColumns } from 'cb-utils/console-entity-models';

const Wrapper = styled.div`
  width: 100%;
`;

const CardBox = styled.div`
  display: flex;
  margin-bottom: 2rem;
  height: 14rem;
`;

type MessagesDetailProps = SystemRouteProps<{
  messageTopic: string;
}>;

const getTopicName = (props: MessagesDetailProps): string => {
  // only way to handle manually typed url parsing to see if user is wanting to
  // subscribe to multi-level wildcard
  if (window.location.href.slice(-1) === '#') {
    return `${props.match.params.messageTopic}/#`;
  }
  return props.match.params.messageTopic;
};

export class MessagesDetail extends React.PureComponent<
  MessagesDetailProps & DispatchToProps & StateToProps,
  {}
> {
  componentDidMount() {
    if (!isWildcard(getTopicName(this.props))) {
      this.props.fetchMessages();
    }
  }

  render() {
    const isEdge = !!this.props.match.params.edgeName;
    const topicName = getTopicName(this.props);
    return (
      <ConsolePage subheader={this.renderSubheader(topicName, isEdge)}>
        <Helmet
          title="ClearBlade - Messages"
          meta={[
            { name: 'description', content: 'Description of MessagesDetail' },
          ]}
        />
        <Wrapper>
          <CardBox>
            {!isWildcard(topicName) && (
              <MessageQueryContainer
                fetchMessages={this.props.fetchMessages}
                deleteMessages={this.props.deleteMessages}
              />
            )}
            {!isEdge ? <LiveMessageContainer topicName={topicName} /> : null}
          </CardBox>
          {!isWildcard(topicName) && (
            <React.Fragment>
              <h5>
                <FormattedMessage {...messages.history} />
              </h5>
              <MessageHistoryGrid
                data={this.props.messages}
                isLoading={this.props.isLoading}
                columnNames={[
                  MessageColumns.MESSAGE,
                  MessageColumns.SEND_DATE,
                  MessageColumns.USER_ID,
                  MessageColumns.RESEND,
                ]}
                showModal={this.props.showMessageModal}
              />
            </React.Fragment>
          )}
        </Wrapper>
      </ConsolePage>
    );
  }

  renderSubheader = (topicName: string, isEdge: boolean) => {
    return (
      <PageHeader
        header={<div>{topicName}</div>}
        subHeader={!isEdge ? <MqttConnection topic={topicName} /> : null}
      />
    );
  };
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  messages: makeSelectMessages(),
  isLoading: makeSelectIsLoading(),
});

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: MessagesDetailProps,
) {
  const topicName = getTopicName(ownProps);
  return {
    fetchMessages: () => dispatch(createFetchMessagesRequest(topicName)),
    deleteMessages: () =>
      dispatch(
        createShowConfirmationModal(
          'Are you sure you want to delete all filtered messages?',
          createDeleteMessagesRequest(topicName),
        ),
      ),
    showMessageModal: (component: CustomModalBodyComponents, props: object) =>
      dispatch(createShowBaseModal({ component, props })),
    clearMessages: () => dispatch(createClearMessages()),
  };
}

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(MessagesDetail);



