/*
 *
 * LiveMessageContainer reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';

export interface State {
  liveMessages: string[];
  showFeed: boolean;
  isSubscribed: boolean;
}

export const initialState: State = {
  liveMessages: [],
  showFeed: false,
  isSubscribed: false,
};

function liveMessageContainerReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.DEFAULT_ACTION:
      return state;
    case ActionConstants.NEW_MESSAGE_RECEIVED:
      return assign(state, (m) => {
        m.liveMessages = [...m.liveMessages, action.payload.msg];
        return m;
      });
    case ActionConstants.CLEAR_LIVE_MESSAGES:
      return assign(state, (m) => {
        m.liveMessages = [];
        return m;
      });
    case ActionConstants.EXPAND_FEED:
      return assign(state, (m) => m.showFeed, () => true);
    case ActionConstants.HIDE_FEED:
      return assign(state, (m) => m.showFeed, () => false);
    case ActionConstants.SUBSCRIBE_MQTT_SUCCESS:
      return assign(state, (m) => m.isSubscribed, () => true);
    case ActionConstants.UNSUBSCRIBE_MQTT_SUCCESS:
      return assign(state, (m) => m.isSubscribed, () => false);
    default:
      return state;
  }
}

export default liveMessageContainerReducer;



