import { all, takeEvery, call, put, select } from 'redux-saga/effects';
import { ActionConstants } from './actionConstants';
import {
  FetchMessagesRequest,
  DeleteMessagesRequest,
  createFetchMessagesSuccess,
  createFetchMessagesRequest,
  createFetchMessagesError,
} from 'containers/MessagesDetail/actions';
import {
  selectTimeframe,
  selectCount,
  selectLast,
  selectStart,
  selectStop,
} from 'containers/MessageQueryContainer/selectors';
import { getMessageHistory, deleteMessages } from 'cb-utils/services/messages';
import { MessageModel } from 'cb-utils/console-entity-models';
import { createNewNotification } from 'containers/Console/actions/ui';
import { createChangeCount } from 'containers/MessageQueryContainer/actions';

function* callFetchMessages(action: FetchMessagesRequest) {
  try {
    const stringParams = yield call(getStringParams, action.payload.topic);
    const mess: MessageModel[] = yield call(getMessageHistory, stringParams);
    mess.sort((a, b) => b['send-date'] - a['send-date']);
    yield put(createFetchMessagesSuccess(mess));
  } catch (e) {
    yield put(createFetchMessagesError());
    yield put(
      createNewNotification({
        title: `Error fetching messages`,
        className: 'error',
      }),
    );
  }
}

function* callDeleteMessages(action: DeleteMessagesRequest) {
  try {
    const stringParams = yield call(getStringParams, action.payload.topic);
    yield call(deleteMessages, stringParams);
    yield put(
      createNewNotification({
        title: `Message(s) successfully deleted`,
      }),
    );
    yield put(createChangeCount(25));
    yield put(createFetchMessagesRequest(action.payload.topic));
  } catch (e) {
    yield put(
      createNewNotification({
        title: `Error deleting message(s)`,
        className: 'error',
      }),
    );
    throw new Error(e);
  }
}

function* getStringParams(topic: string) {
  const isTimeframe = yield select(selectTimeframe());
  const count = yield select(selectCount());
  const last = yield select(selectLast());
  const start = yield select(selectStart());
  const stop = yield select(selectStop());
  let stringParams = `topic=${topic}`;
  if (isTimeframe) {
    stringParams = `${stringParams}&start=${Math.floor(
      start / 1000,
    )}&stop=${Math.floor(stop / 1000)}`;
  } else {
    stringParams = `${stringParams}&last=${Math.floor(last / 1000)}`;
  }
  stringParams = count > 0 ? `${stringParams}&count=${count}` : stringParams;
  return stringParams;
}

// Individual exports for testing

export function* fetchMessages() {
  yield takeEvery(ActionConstants.FETCH_MESSAGES_REQUEST, callFetchMessages);
}

export function* deleteBulkMessages() {
  yield takeEvery(ActionConstants.DELETE_MESSAGES_REQUEST, callDeleteMessages);
}

// All sagas to be loaded
export default function*() {
  yield all([fetchMessages(), deleteBulkMessages()]);
}



