/*
 *
 * MessageQueryContainer actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';

export interface DefaultAction extends BaseAction {
  type: ActionConstants.DEFAULT_ACTION;
}

export const createDefaultAction = (): DefaultAction => ({
  type: ActionConstants.DEFAULT_ACTION,
});

interface ChangeTimeConstraint extends BaseAction {
  type: ActionConstants.CHANGE_TIME_CONSTRAINT;
  payload: {
    isTimeframe: boolean;
  };
}

export const createChangeTimeConstraint = (isTimeframe: boolean) => ({
  type: ActionConstants.CHANGE_TIME_CONSTRAINT,
  payload: {
    isTimeframe,
  },
});

interface ChangeCount extends BaseAction {
  type: ActionConstants.CHANGE_COUNT;
  payload: {
    count: number;
  };
}

export const createChangeCount = (count: number) => ({
  type: ActionConstants.CHANGE_COUNT,
  payload: {
    count,
  },
});

interface ChangeLast extends BaseAction {
  type: ActionConstants.CHANGE_LAST;
  payload: {
    last: number;
  };
}

export const createChangeLast = (last: number) => ({
  type: ActionConstants.CHANGE_LAST,
  payload: {
    last,
  },
});

interface ChangeStart extends BaseAction {
  type: ActionConstants.CHANGE_START;
  payload: {
    start: number;
  };
}

export const createChangeStart = (start: number) => ({
  type: ActionConstants.CHANGE_START,
  payload: {
    start,
  },
});

interface ChangeStop extends BaseAction {
  type: ActionConstants.CHANGE_STOP;
  payload: {
    stop: number;
  };
}

export const createChangeStop = (stop: number) => ({
  type: ActionConstants.CHANGE_STOP,
  payload: {
    stop,
  },
});

interface ClearQueries extends BaseAction {
  type: ActionConstants.CLEAR_QUERIES;
}

export const createClearQueries = () => ({
  type: ActionConstants.CLEAR_QUERIES,
});

export type ActionTypes =
  | DefaultAction
  | ChangeTimeConstraint
  | ChangeCount
  | ChangeLast
  | ChangeStart
  | ChangeStop
  | ClearQueries;



