/**
 *
 * MessageHistoryGrid
 *
 */

import React from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import { withRouter } from 'react-router';
import messages from './messages';
import { MessageModel, MessageColumns } from 'cb-utils/console-entity-models';
import ReactTable, { Column, FinalState } from 'react-table';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import 'react-table/react-table.css';
import { Link } from 'react-router-dom';
import { CustomModalBodyComponents } from 'components/BaseModal';
import { format } from 'date-fns';
import CrudGridPagination from 'components/CrudGridPagination';
import CopyInput from '../CopyInput';

const GridWrapper = styled.div`
  height: 100%;
  overflow-y: scroll;
`;

interface MessageHistoryGridProps extends SystemRouteProps<{}> {
  data: MessageModel[];
  isLoading: boolean;
  showModal: (component: CustomModalBodyComponents, props: object) => void;
  isSearch?: boolean;
  columnNames: string[];
}

class MessageHistoryGrid extends React.PureComponent<
  MessageHistoryGridProps,
  {}
> {
  // eslint-disable-line react/prefer-stateless-function
  static defaultProps: Partial<MessageHistoryGridProps> = {
    isLoading: false,
  };

  render() {
    return (
      <GridWrapper>
        <ReactTable
          data={this.props.data}
          sortable={false}
          defaultPageSize={this.props.isSearch ? 1 : 15}
          pageSizeOptions={[5, 10, 15, 20, 25]}
          filterable={false}
          loading={this.props.isLoading}
          columns={this.createColumns(this.props.data)}
          showPagination={this.props.isSearch ? false : true}
          PaginationComponent={this._createPaginationComponent}
        />
      </GridWrapper>
    );
  }

  _createPaginationComponent = (state: FinalState) => {
    return (
      <CrudGridPagination gridState={state} total={this.props.data.length} />
    );
  };

  createColumns = (data: MessageModel[]) => {
    const columns: Column[] = [];
    if (data.length > 0) {
      const columnNames = this.props.columnNames;
      columnNames.forEach((key) => {
        const col: Column = {
          Header: key,
          accessor: key,
        };
        col.Cell = this._renderCell;
        columns.push(col);
      });
    }
    return this._transformColumns(columns);
  };

  _formatCellValue = (value: any) => {
    return typeof value === 'boolean' ? value.toString() : value;
  };

  _renderCell = (cellInfo: any) => {
    return (
      <div>
        {this._formatCellValue(
          this.props.data[cellInfo.index][cellInfo.column.id],
        )}
      </div>
    );
  };

  _transformColumns = (columns: Column[]) => {
    const newCols = columns.map((col) => {
      const colName = col.accessor;
      switch (colName) {
        case MessageColumns.TOPIC_ID:
          col.Header = <FormattedMessage {...messages.topic} />;
          col.Cell = (cellInfo) => {
            const val = cellInfo.row[colName];
            return <Link to={`${this.props.match.url}/${val}`}>{val}</Link>;
          };
          col.maxWidth = 400;
          return col;
        case MessageColumns.SEND_DATE:
          col.Header = <FormattedMessage {...messages.received} />;
          col.Cell = (cellInfo) => {
            return (
              cellInfo.value &&
              format(new Date(cellInfo.value * 1000), `MMM DD YYYY, hh:mm a`)
            );
          };
          col.maxWidth = 190;
          return col;
        case MessageColumns.MESSAGE:
          col.Header = this.props.isSearch ? (
            <FormattedMessage {...messages.lastMessage} />
          ) : (
            <FormattedMessage {...messages.message} />
          );
          col.Cell = (cellInfo) => cellInfo.value;
          return col;
        case MessageColumns.USER_ID:
          col.Header = <FormattedMessage {...messages.userId} />;
          col.Cell = (cellInfo) => (
            <CopyInput value={cellInfo.value} isGridCell />
          );
          col.maxWidth = 320;
          return col;
        case MessageColumns.RESEND:
          col.Header = '';
          col.Cell = (rowInfo) => {
            return (
              <a
                href=""
                data-cy="message-resend-link"
                onClick={(e) => {
                  e.preventDefault();
                  this.props.showModal('MessagesModal', {
                    message: rowInfo.original,
                    isDetail: !this.props.isSearch,
                  });
                }}
                id="resend-message"
              >
                <FormattedMessage {...messages.view} />
              </a>
            );
          };
          col.maxWidth = 120;
          return col;
        default:
          return col;
      }
    });
    return newCols;
  };
}

export default withRouter(MessageHistoryGrid);



