/*
 *
 * MessageQueryContainer reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';

export interface State {
  isTimeframe: boolean;
  lastDate: number;
  startDate: number;
  stopDate: number;
  count: number;
}

const date = new Date().getTime();
const ONE_YEAR = 31556952000;
const lastDate = date - ONE_YEAR;
export const initialState = {
  isTimeframe: false,
  lastDate: date,
  startDate: lastDate,
  stopDate: date,
  count: 25,
};

function messageQueryContainerReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.DEFAULT_ACTION:
      return state;
    case ActionConstants.CHANGE_TIME_CONSTRAINT:
      return assign(
        state,
        (s) => s.isTimeframe,
        () => action.payload.isTimeframe,
      );
    case ActionConstants.CHANGE_COUNT:
      return assign(state, (s) => s.count, () => action.payload.count);
    case ActionConstants.CHANGE_LAST:
      return assign(state, (s) => s.lastDate, () => action.payload.last);
    case ActionConstants.CHANGE_START:
      return assign(state, (s) => s.startDate, () => action.payload.start);
    case ActionConstants.CHANGE_STOP:
      return assign(state, (s) => s.stopDate, () => action.payload.stop);
    case ActionConstants.CLEAR_QUERIES:
      return assign(state, () => initialState);
    default:
      return state;
  }
}

export default messageQueryContainerReducer;



