/*
 *
 * MessagesDetail reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import { MessageModel } from 'cb-utils/console-entity-models';

export interface State {
  messageHistory: MessageModel[];
  isLoading: boolean;
}

export const initialState: State = {
  messageHistory: [],
  isLoading: false,
};

function messagesDetailReducer(
  state: State = initialState,
  action: ActionTypes,
) {
  switch (action.type) {
    case ActionConstants.DEFAULT_ACTION:
      return state;
    case ActionConstants.FETCH_MESSAGES_REQUEST:
      return assign(state, (m) => m.isLoading, () => true);
    case ActionConstants.FETCH_MESSAGES_SUCCESS:
      return assign(state, (m) => {
        m.isLoading = false;
        m.messageHistory = action.payload.messages;
        return m;
      });
    case ActionConstants.FETCH_MESSAGES_ERROR:
      return assign(state, (m) => m.isLoading, () => false);
    default:
      return state;
  }
}

export default messagesDetailReducer;



