import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the messagesDetail state domain
 */
const selectMessagesDetailDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */
const makeSelectMessages = () =>
  createSelector(
    selectMessagesDetailDomain(),
    (substate) => substate.messageHistory,
  );

const makeSelectIsLoading = () =>
  createSelector(
    selectMessagesDetailDomain(),
    (substate) => substate.isLoading,
  );

/**
 * Default selector used by MessagesDetail
 */

const makeSelectMessagesDetail = () =>
  createSelector(selectMessagesDetailDomain(), (substate) => substate);

export default makeSelectMessagesDetail;
export { selectMessagesDetailDomain, makeSelectMessages, makeSelectIsLoading };



